import React from "react";
import { Header } from "@massds/mayflower-react";
import MainNavData from "../data/MainNav.data";
import UtilityNavData from "../data/UtilityNav.data";
import HeaderSearchData from "../data/HeaderSearch.data";
import "../style.css";

class MainHeader extends React.Component {
  constructor(props) {
    super(props);
    // this.footerProps = {
    //   footerLinks: FooterData.footerLinks,
    //   socialLinks: SocialLinksLiveData.socialLinks
    // };
    MainNavData.mainNav.forEach(navItem => {
      if (navItem.text === this.props.active) {
        navItem.active = true;
      }
    });
    console.log(MainNavData.mainNav);
    this.headerProps = {
      utilityNav: UtilityNavData,
      headerSearch: HeaderSearchData,
      mainNav: MainNavData,
      hideHeaderSearch: false,
      hideBackTo: true,
      siteLogoDomain: { url: { domain: "https://www.mass.gov/" } }
    };
  }
  render() {
    return <Header {...this.headerProps} />;
  }
}
export default MainHeader;
