import React from "react";
import OurOrganizations from "./OurOrganizations";
import EOTSSPolicies from "./EOTSSPolicies";
import NewsAndAnnouncements from "./NewsAndAnnouncements";
import Events from "./Events";
import Careers from "./Careers";
import ContactUs from "./ContactUs";
import Feedback from "./Feedback";
import ActionCards from "./ActionCards";
import AboutUs from "./AboutUs";
import { ColoredHeading } from "@massds/mayflower-react";
class EotssLandingPage extends React.Component {
  render() {
    return (
      <main className="main-content">
        <section className="main-content--two">
          <div className="ma__page-header__content">
            {/* <h1 className="ma__page-header__title">
            Mayflower Starter Template
          </h1> */}
            <ActionCards />
            <div style={{ height: "40px" }} />
            <ColoredHeading color="green" level={2} text="About Us" />
            <div style={{ height: "30px" }} />
            <AboutUs />
            <ColoredHeading color="green" level={2} text="Our Organizations" />
            <OurOrganizations />
            <ColoredHeading color="green" level={2} text="EOTSS Policies" />
            <EOTSSPolicies />
            <ColoredHeading
              color="green"
              level={2}
              text="Recent News and Announcements"
            />
            <NewsAndAnnouncements />
            <ColoredHeading color="green" level={2} text="Events" />
            <Events />
            <ColoredHeading color="green" level={2} text="Careers" />
            <Careers />
            <ColoredHeading color="green" level={2} text="Contact Us" />
            <ContactUs />
            <ColoredHeading color="green" level={2} text="Feedback" />
            <Feedback />
          </div>
        </section>
      </main>
    );
  }
}

export default EotssLandingPage;
