import React from "react";
import { Icon } from "@massds/mayflower-react";

class ActionCard extends React.Component {
  render() {
    return (
      <div
        className={`ma__action-card__item js-clickable ma__action-card__item--${
          this.props.color
        }`}
      >
        <div className="ma__action-card__icon">
          <Icon
            classes={[""]}
            fill="#fffff"
            height={50}
            name={this.props.iconName}
            title="Icon Title Here"
            width={40}
          />
        </div>
        <div className="ma__action-card__content">
          <h3 className="ma__action-card__link">
            <a
              className="js-clickable-link"
              href={this.props.link}
              title={this.props.tagLine}
            >
              {this.props.tagLine}&nbsp;
              <Icon
                classes={[""]}
                fill="#fffff"
                height={90}
                name="arrow"
                title="Icon Title Here"
                width={40}
              />
            </a>
          </h3>
          <span className="ma__action-card__category">
            {this.props.description}
          </span>
        </div>
      </div>
    );
  }
}

export default ActionCard;
