import React from "react";
import * as chatbotUrl from "../Images/MasChatLight.png";
class Chatbot extends React.Component {
  render() {
    return (
      <>
        <input type="hidden" id="tenant_id" value="12345689" />
        <input type="hidden" id="rasa_endpoint" value="" />
        <div className="widget resize-drag" id="widget">
          <div className="chat_header">
            <span title="EOTSS" style={{ color: "white", marginLeft: "5px" }}>
              EOTSS
            </span>
            <span
              title="Close"
              style={{
                color: "white",
                marginRight: "5px",
                float: "right",
                marginTop: "5px"
              }}
              id="close"
            >
              <i className="material-icons">close</i>
            </span>
            <span
              title="Minimize"
              className="action-btns"
              style={{ marginTop: "0px" }}
              id="minimize"
            >
              <i className="material-icons">minimize</i>
            </span>
            <span title="Help" className="action-btns" id="setting">
              <span className="material-icons"> help </span>
              <span title="Help" className="settings" id="settings">
                <div>
                  <span style={{ fontWeight: "600" }}>Shortcuts</span>
                  <hr style={{ borderColor: "#c1ced4" }} />
                  <ul>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Toggle bot:</span>
                        <span className="settings-value">Alt + b</span>
                      </div>
                    </li>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Increase Font:</span>
                        <span className="settings-value">Alt + </span>
                      </div>
                    </li>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Decrease Font:</span>
                        <span className="settings-value">Alt - </span>
                      </div>
                    </li>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Minimize bot:</span>
                        <span className="settings-value">Alt + m </span>
                      </div>
                    </li>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Close bot:</span>
                        <span className="settings-value">Alt + c </span>
                      </div>
                    </li>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Toggle Mic:</span>
                        <span className="settings-value">Alt + r </span>
                      </div>
                    </li>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Send input:</span>
                        <span className="settings-value">Alt + enter </span>
                      </div>
                    </li>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Toggle help:</span>
                        <span className="settings-value">Alt + i </span>
                      </div>
                    </li>
                    <li>
                      <div className="shortcuts-info">
                        <span className="settings-label">Reset bot:</span>
                        <span className="settings-value">Alt + q </span>
                      </div>
                    </li>
                  </ul>
                </div>
              </span>
            </span>
            <span
              title="Enable Visual Enhancement Mode"
              className="action-btns"
              id="visual-enhancement"
            >
              <i className="material-icons">accessible</i>
            </span>
            <span
              title="Descrease Font"
              className="action-btns"
              id="decrease-font"
            >
              A
            </span>
            <span
              title="Increase Font"
              className="action-btns"
              id="increase-font"
            >
              A
            </span>
            <span title="Reset bot" className="action-btns" id="reset-bot">
              <i
                title="Reset bot"
                className="fas fa-history reset-bot"
                style={{ marginTop: "3px" }}
              />
            </span>
            <div>
              <span
                title="Your Personal Assistant"
                style={{
                  color: "white",
                  marginLeft: "5px",
                  fontSize: "small",
                  fontStyle: "italic",
                  display: "block"
                }}
              >
                Your Personal Assistant
              </span>
            </div>
          </div>

          <div className="chats" id="chats">
            <div className="clearfix" />
            <div
              className="testing"
              id="testing"
              style={{ marginTop: "10px" }}
            />
          </div>

          <div className="keypad">
            <input
              type="text"
              id="keypad"
              disabled
              className="usrInput browser-default"
              placeholder="Type a message..."
              autoComplete="off"
            />

            <div title="Mic" className="popup">
              <i
                id="mic-btn-on"
                className="fas fa-microphone-alt fa-2x hide"
                style={{ marginLeft: "3px", marginTop: "5px" }}
              />
              <span className="popuptext" id="myPopup">
                Go on i'm Listening!
              </span>
              <i
                id="mic-btn-off"
                className="fas fa-microphone fa-2x"
                style={{ marginLeft: "3px", marginTop: "5px" }}
              />
            </div>
            <i
              title="Send"
              id="send-btn"
              className="fas fa-paper-plane fa-2x"
              style={{ marginLeft: "3px", marginTop: "5px" }}
            />
            <i
              title="Reset bot"
              className="fas fa-history fa-2x reset-bot"
              style={{
                cursor: "pointer",
                marginLeft: "3px",
                marginTop: "5px",
                fontSize: "32px"
              }}
            />
          </div>
        </div>

        <div id="creya_logo" title="EOTSS Bot">
          <div className="profile_div" id="profile_div">
            <img
              className="imgProfile"
              alt="EOTSS Bot"
              src={chatbotUrl.default}
            />
          </div>
          <div id="bot" className="bot">
            <i id="toggleCreya" className="fas fa-chevron-right fa-2x" />
          </div>
        </div>

        <div className="creya_logo_min" title="Chatbot" id="creya_logo_min">
          <i id="togglecreya_new" className="fas fa-chevron-left" />
          <span
            className="profile_div_tiny"
            id="profile_div_tiny"
            style={{
              fontFamily: "Roboto', sans-serif",
              fontStyle: "italics"
            }}
          >
            EOTSS
          </span>
        </div>
      </>
    );
  }
}

export default Chatbot;
