import React from "react";
import { Heading, Paragraph } from "@massds/mayflower-react";
import { Button } from "@massds/mayflower-react";
class Events extends React.Component {
  render() {
    return (
      <>
        <section className="ma__split-columns">
          <div className="ma__split-columns__container">
            <div className="ma__split-columns__column careers-paragraph">
              <section
                className="ma__event-listing  js-event-listing"
                id="events"
              >
                <div className="ma__event-listing__container">
                  <Heading
                    level={3}
                    text="Technology Careers at the Commonwealth of Massachusetts"
                  />
                  {/* <div className="ma__event-listing__empty">
                    No upcoming events scheduled
                  </div> */}
                  <Paragraph
                    className="ma__custom-class"
                    text="State government is truly a digital business. A dedicated, diverse team of over 1,500 mission-driven IT professionals across 150 Massachusetts agencies supports vital work that touches virtually every aspect of our lives."
                  />
                  <div style={{ height: "10px" }} />
                  <Button
                    classes={[""]}
                    disabled={false}
                    href="/"
                    info="Learn More"
                    onClick={function noRefCheck() {
                      window.location =
                        "https://www.mass.gov/technology-careers-at-the-commonwealth-of-massachusetts";
                    }}
                    size=""
                    text="Learn More"
                    theme=""
                    type=""
                    usage=""
                  />
                </div>
              </section>
            </div>
            <div className="ma__split-columns__column career-video">
              {/* <Heading level={3} text="Why Work for the Commonwealth?" />
              <div style={{ height: "10px" }} /> */}
              <section className="ma__event-listing-interactive js-event-listing-interactive">
                <div className="ma__event-listing-interactive__results js-event-listing-results">
                  <section className="ma__event-listing  js-event-listing">
                    <div className="ma__event-listing__container">
                      <div className="ma__video ma__video--new ma__video--left">
                        <div className="ma__video__container js-ma-responsive-video">
                          <Heading
                            level={6}
                            text="Why Work for the Commonwealth?"
                            class="text-center"
                          />
                          <div style={{ height: "10px" }} />
                          <iframe
                            // width="500"
                            // height="250"
                            src="https://www.youtube.com/embed/55oIZ1L-5Ak"
                            allowFullScreen
                            allow="accelerometer; fullscreen; gyroscope; picture-in-picture"
                            title="Using a gas grill safely"
                          />
                        </div>
                      </div>
                    </div>
                  </section>
                </div>
              </section>
              <div style={{ height: "10px" }} />
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default Events;
