import React from "react";
import { LinkList } from "@massds/mayflower-react";
import { Button } from "@massds/mayflower-react";

class EOTSSPolicies extends React.Component {
  render() {
    return (
      <>
        <LinkList
          className="center"
          description={{
            text:
              "The EOTSS Enterprise Security Office is responsible for writing, publishing, and updating all Enterprise Information Security and Policies and Standards that apply to all Executive Department Office and agencies. This is a compilation of those policies and standards"
          }}
          links={[
            {
              href:
                "https://www.mass.gov/policy-advisory/enterprise-information-security-policy",
              info: "",
              text: "IS.000 Enterprise Information Security Policy"
            },
            {
              href:
                "https://www.mass.gov/advisory/organization-of-information-security-standard",
              info: "",
              text: "IS.001 Organization of Information Security Standard"
            },
            {
              href:
                "https://www.mass.gov/policy-advisory/acceptable-use-of-information-technology-policy",
              info: "",
              text: "IS.002 Acceptable Use of Information Technology Policy"
            },
            {
              href: "https://www.mass.gov/advisory/access-management-standard",
              info: "",
              text: "IS.003 Access Management Standard"
            },
            {
              href: "https://www.mass.gov/advisory/asset-management-standard",
              info: "",
              text: "IS.004 Asset Management Standard"
            },
            {
              href:
                "https://www.mass.gov/advisory/business-continuity-and-disaster-recovery-standard",
              info: "",
              text: "IS.005 Business Continuity and Disaster Recovery Standard"
            },
            {
              href:
                "https://www.mass.gov/advisory/communication-and-network-security-standard",
              info: "",
              text: "IS.006 Communication and Network Security Standard "
            },
            {
              href: "https://www.mass.gov/policy-advisory/compliance-standard",
              info: "",
              text: "IS.007 Compliance Standard"
            },
            {
              href:
                "https://www.mass.gov/advisory/cryptographic-management-standard",
              info: "",
              text: "IS.008 Cryptographic Management Standard"
            },
            {
              href:
                "https://www.mass.gov/advisory/information-security-incident-management-standard",
              info: "",
              text: "IS.009 Information Security Incident Management Standard"
            },
            {
              href:
                "https://www.mass.gov/advisory/information-security-risk-management-standard",
              info: "",
              text: "IS.010 Information Security Risk Management Standard  "
            }
          ]}
          stacked
        />
        <div className="button-center">
          <Button
            classes={[""]}
            disabled={false}
            href="https://www.mass.gov/handbook/enterprise-information-security-policies-and-standards"
            info="Read More Policies and Standards"
            onClick={function noRefCheck() {
              window.location = this.href;
            }}
            text="Read More"
          />
        </div>
      </>
    );
  }
}

export default EOTSSPolicies;
