import React from "react";
import * as url from "../Images/curt.jfif";
class AboutUs extends React.Component {
  render() {
    return (
      <>
        <div className="ma__about-section">
          <div className="ma__about-section__content">
            <div className="ma__about-section__content-container">
              <div className="ma__about-section__content-text">
                <div className="ma__rich-text ">
                  <p>
                    Curt Wood serves as the Cabinet Secretary and Commonwealth’s
                    Chief Information Officer, overseeing the Executive Office
                    of Technology Services and Security (EOTSS), whose mission
                    is to provide secure and quality digital information,
                    services, and tools  to constituents and service providers
                    when and where they need them. Governor Baker created EOTSS,
                    formerly MassIT, in 2017 to standardize, centralize, and
                    secure infrastructure and core services.
                  </p>
                </div>

                <a className="ma__content-link " href="/">
                  <span>Read more about EOTSS</span>
                </a>
              </div>

              <div className="ma__about-section__person">
                <div className="ma__person-card">
                  <div className="ma__person-card__wrapper">
                    <div className="ma__person-card__image">
                      <img
                        className="ma__image circular"
                        alt="alt text"
                        src={url.default}
                        height="250"
                        width="250"
                      />
                    </div>
                    <div className="ma__person-card__person-info">
                      <div className="ma__person-card__name">
                        <h2
                          className="ma__comp-heading ma__comp-heading--transparent ma__comp-heading--centered  "
                          tabIndex="-1"
                        >
                          Curt M. Wood
                        </h2>
                      </div>
                      <div className="ma__person-card__title">
                        <h3 className="ma__column-heading">
                          Chief Information Officer
                        </h3>
                      </div>
                    </div>
                    <div className="ma__person-card__links">
                      <div className="ma__divider" />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default AboutUs;
